/* Общие стили */
*, *:after, *:before {
	box-sizing: border-box;
}

.usz {
  margin-left: -60px;
  margin-right: -80px;
}
.clearfix:before, .clearfix:after {
	content: '';
	display: table;
}
.clearfix:after {
	clear: both;
}
.hover-text-one {
	overflow: hidden;
	width: 66%;
	list-style: none;
	text-align: center;
  margin-right: -20px;
  margin-left: -20px;
}
.hover-text-one figure {
	position: relative;
	z-index: 1;
	display: block;
	overflow: hidden;
	background: #000000;
	text-align: center;
	cursor: pointer;
}
.hover-text-one figure img {
	position: relative;
	display: block;
	opacity: 0.4;
  height: 300px;
}
.hover-text-one figure figcaption {
	padding: 2em;
	color: #fff;
	text-transform: uppercase;
	font-size: 1.25em;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}
.hover-text-one figure figcaption::before,
.hover-text-one figure figcaption::after {
	pointer-events: none;
}
.hover-text-one figure figcaption{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.hover-text-one figure h3 {

	font-weight: 300;
}
.hover-text-one figure h3 span {
	font-weight: 800;
}
.hover-text-one figure h3,
.hover-text-one figure p {
	margin: 0;
}
.hover-text-one figure p {

	font-size: 68.5%;
}





